<template>
    <a type="button" @click.prevent="addPlayer" class="add-player">Добавить игрока</a>
</template>

<script>
export default {
    props: [],
    methods: {
        addPlayer() {
            this.$emit('add-player');
        }
    }
}
</script>

<style lang="scss" scoped>
.add-player {
    cursor: pointer;
    text-align: center;
    color: #fff;
    display: block;
    padding: 5px;
    background: #FF7052;
    text-decoration: none;
    position: absolute;
    bottom: 10px;
    left: 5px;
    right: 5px;
}
</style>